:root {
  --font-body: "Manrope", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --verydarkgrayishblue: hsl(217, 19%, 35%);
  --desaturateddarkblue: hsl(214, 17%, 51%);
  --grayishblue: hsl(212, 23%, 69%);
  --lightgrayishblue: hsl(210, 46%, 95%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 13px;
  background-color: var(--lightgrayishblue);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: red;
}

.container {
  margin: 0;
  padding: 0;
  height: 100vh;
  max-width: 90rem;
  max-height: 50rem;
}

.wrapper {
  display: grid;
  grid-template-columns: 285px 445px;
  grid-template-rows: 280px;
  justify-content: center;
  align-content: center;
  height: 95vh;
}
@media (max-width: 48em) {
  .wrapper {
    grid-template-columns: minmax(360px, 740px);
    height: 80vh;
    margin-top: 4rem;
  }
}
@media (max-width: 25.9375em) {
  .wrapper {
    grid-template-columns: 326px;
    margin-top: 4rem;
    grid-template-rows: 200px 304px;
  }
}

.picture {
  background-image: url(../app/images/drawers.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: aquamarine;
  border-radius: 10px 0 0 10px;
}
@media (max-width: 48em) {
  .picture {
    border-radius: 10px 10px 0 0;
  }
}
@media (max-width: 25.9375em) {
  .picture {
    height: 200px;
    border-radius: 10px 10px 0 0;
  }
}

.story {
  position: relative;
  border-radius: 0 10px 10px 0;
  background-color: #fff;
  padding: 3rem;
}
@media (max-width: 48em) {
  .story {
    border-radius: 0 0 10px 10px;
  }
}
@media (max-width: 25.9375em) {
  .story {
    border-radius: 0 0 10px 10px;
    padding: 2.2rem;
  }
}
.story h1 {
  color: var(--verydarkgrayishblue);
  line-height: 1.4;
}
@media (max-width: 25.9375em) {
  .story h1 {
    font-size: 1rem;
  }
}
.story p {
  margin-top: 1rem;
  color: var(--desaturateddarkblue);
  line-height: 1.6;
}

.details {
  display: flex;
}
@media (max-width: 48em) {
  .details {
    justify-content: space-around;
  }
}
@media (max-width: 25.9375em) {
  .details {
    margin-top: 1.5rem;
  }
}
.details .avatar {
  border-radius: 50%;
  width: 40px;
}
.details .info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.details .info h2 {
  display: block;
  padding-left: 1rem;
  font-size: 0.9em;
  color: var(--verydarkgrayishblue);
}
.details .info h3 {
  display: block;
  padding-left: 1rem;
  font-size: 0.9em;
  color: var(--grayishblue);
}
.details .share {
  display: flex;
  align-items: center;
  margin-left: auto;
  background-color: var(--desaturateddarkblue);
  border-radius: 50%;
  padding: 13px;
  cursor: pointer;
}
@media (max-width: 48em) {
  .details .share {
    margin-left: 0;
  }
}
@media (max-width: 25.9375em) {
  .details .share {
    background-color: var(--grayishblue);
    margin-left: auto;
  }
}
.details .share svg {
  position: relative;
  margin-left: auto;
  cursor: pointer;
}
.details .share #icons {
  display: none;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  top: -90px;
  left: -95px;
  background-color: var(--verydarkgrayishblue);
  color: #fff;
  width: 245px;
  height: 55px;
  border-radius: 10px;
}
@media (max-width: 25.9375em) {
  .details .share #icons {
    justify-content: start;
    width: 326px;
    border-radius: 0 0 10px 10px;
    left: -262px;
    top: -26px;
    height: 64px;
  }
}
.details .share #icons p {
  text-transform: uppercase;
  letter-spacing: 5px;
  color: var(--grayishblue);
  font-size: 0.9em;
}
@media (max-width: 25.9375em) {
  .details .share #icons p {
    margin-left: 2rem;
  }
}
.details .share #icons img {
  display: block;
  border-radius: 3px;
  width: 20px;
}
@media (max-width: 25.9375em) {
  .details .share #icons img {
    margin-left: 1rem;
  }
}
.details .share #icons::before {
  content: "";
  position: relative;
  display: none;
}
.details .share #icons::after {
  content: "";
  position: relative;
  top: 32px;
  left: -108px;
  width: 0;
  height: 0;
  border-top: 10px solid var(--verydarkgrayishblue);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
}
@media (max-width: 25.9375em) {
  .details .share #icons::after {
    display: none;
  }
}

.wtf {
  position: relative;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
@media (max-width: 48em) {
  .attribution {
    margin-top: 3rem;
  }
}

.attribution a {
  color: #3e52a3;
}